import React, { Component } from "react";
import "./App.css";
import Form from "./Components/Form";
import List from "./Components/List";

class App extends Component {
  constructor() {
    super();
    this.state = {
      value: [
        {
          inputValue: "",
          id: "",
        },
      ],
    };
  }

  onFetchValueFromForm(formInputValue, formId) {
    this.setState((prevValue) => {
      return {
        value: [
          ...prevValue.value,
          {
            inputValue: formInputValue,
            id: Math.random() + 1,
          },
        ],
      };
    });
  }

  removeTodo(formValue) {
    const newValue = this.state.value.filter((stateValue) => {
      return stateValue.id !== formValue.id;
    });
    this.setState(() => {
      return {
        value: [...newValue],
      };
    });
  }

  render() {
    return (
      <div className="App">
        <Form fetchFormValue={this.onFetchValueFromForm.bind(this)}></Form>
        <ul className="ul-class">
          {this.state.value.map((eachValue, index) => {
            return (
              <List
                key={index}
                removeTodo={this.removeTodo.bind(this)}
                formValue={eachValue}
              />
            );
          })}
        </ul>
      </div>
    );
  }
}

export default App;
