import { Component } from "react";

class Form extends Component {
  constructor() {
    super();
    this.state = {
      input: "",
    };
  }

  onChangeHandler(event) {
    this.setState({
      input: event.target.value,
      id: "",
    });
  }

  onSubmitHandler(e) {
    e.preventDefault();
    this.props.fetchFormValue(this.state.input);
    this.setState({
      input: "",
    });
  }

  render() {
    return (
      <form onSubmit={this.onSubmitHandler.bind(this)} style={formStyle}>
        <label htmlFor="input">Enter a Todo</label>
        <input
          style={InputBox}
          type="text"
          onChange={this.onChangeHandler.bind(this)}
          value={this.state.input}
          required
        ></input>
        <button type="submit" style={BtnStyle}>
          Submit
        </button>
      </form>
    );
  }
}

const formStyle = {
  display: "flex",
  flexDirection: "column",
  marginTop: "4rem",
  width: "30%",
  height: "auto",
};

const InputBox = {
  margin: "1rem",
  padding: "0.4rem",
};

const BtnStyle = {
  backgroundColor: "red",
  border: "none",
  borderRadius: "0.5rem",
  padding: "0.6rem 0",
  margin: "0 auto",
  width: "40%",
  fontSize: "1rem",
  color: "white",
};

export default Form;
