import React, { Component } from "react";

export default class List extends Component {
  removeItem() {
    this.props.removeTodo(this.props.formValue);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.formValue.inputValue && (
          <li
            id={this.props.formValue.id}
            onClick={this.removeItem.bind(this)}
            style={{
              textAlign: "left",
              margin: "0.2rem auto",
              backgroundColor: "Grey",
              padding: "0.4rem 0.2rem",
              color: "White",
            }}
          >
            {this.props.formValue.inputValue}{" "}
            <span
              style={{ float: "right", color: "Yellow", marginRight: "1rem" }}
            >
              X
            </span>
          </li>
        )}
      </React.Fragment>
    );
  }
}
